.post-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.post {
    border: 1px solid #eaeaea;
    border-radius: 5px;
    padding: 15px;
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

button {
    padding: 8px 12px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

button:hover {
    background-color: #0056b3;
}

span {
    margin-left: 10px;
    font-weight: bold;
}

