/* src/styles.css */

body {
  font-family: 'Arial', sans-serif;
  background-color: #fafafa; /* Light background similar to Instagram */
  margin: 0;
  padding: 0;
  color: #262626; /* Dark text color */
}

h2 {
  color: #333; /* Darker color for headers */
}

a {
  text-decoration: none;
  color: inherit; /* Inherit color from parent */
}

button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: rgba(0, 0, 0, 0.1); /* Light hover effect */
}

