.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #ffffff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.header h1 {
    font-size: 24px;
    color: #333;
}

.header input {
    padding: 8px;
    border-radius: 5px;
    border: 1px solid #eaeaea;
    width: 300px;
}
