* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif; /* Use a clean font */
}

.home-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background: linear-gradient(135deg, #48b0da, #10bec4); /* Gradient background */
}

.main-content {
    display: flex;
    flex: 1;
}

.feed {
    flex: 1;
    padding: 20px;
    margin-left: 20px; /* Space for the sidebar */
    overflow-y: auto; /* Enable scrolling if content exceeds view */
}

.status-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    background-color: rgba(84, 58, 58, 0.9); /* Slightly transparent white */
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Soft shadow */
    margin-bottom: 20px;
}

.status {
    font-size: 18px;
    color: #333;
    font-weight: bold; /* Make the status bold */
}

.like-symbols {
    display: flex;
    align-items: center;
}

.heart-icon {
    font-size: 24px;
    margin: 0 5px;
    cursor: pointer;
    transition: transform 0.2s ease; /* Smooth scale effect */
}

.heart-icon:hover {
    transform: scale(1.3); /* Increase size on hover */
}

.explore-icon {
    font-size: 24px;
    cursor: pointer;
    color: #007bff; /* Bright blue for explore icon */
    transition: transform 0.2s ease;
}

.explore-icon:hover {
    transform: scale(1.3); /* Increase size on hover */
}

/* Post List Styles */
.post {
    padding: 15px;
    border: 1px solid #f1dede;
    border-radius: 10px;
    margin: 10px 0; /* Vertical spacing between posts */
    background-color: #fff; /* White background for posts */
    transition: box-shadow 0.3s ease; /* Smooth shadow transition */
}

.post:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Shadow on hover */
}

.post h3 {
    color: #333;
    font-size: 20px;
    margin-bottom: 10px; /* Space below the title */
}

.post p {
    color: #555; /* Slightly lighter color for post content */
    font-size: 16px;
    margin-bottom: 15px; /* Space below content */
}

.post-actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.post button {
    background-color: #ff3366; /* Eye-catching like button color */
    color: white;
    border: none;
    padding: 8px 12px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease; /* Smooth color transition */
}

.post button:hover {
    background-color: #ff1f3d; /* Darker shade on hover */
}

/* Additional Responsiveness */
@media (max-width: 768px) {
    .status-bar {
        flex-direction: column; /* Stack items on smaller screens */
        align-items: flex-start;
    }

    .like-symbols {
        margin-top: 5px; /* Space above like symbols */
    }

    .feed {
        margin-left: 0; /* Remove left margin for smaller screens */
    }
}

