:root {
    --gradient-start: #faeff3; /* Light pink */
    --gradient-middle: #d4d8c7; /* Orange */
    --gradient-end: #e5dade; /* Bright pink */
}.sidebar {
    width: 200px;
    padding: 20px;
    background: linear-gradient(135deg, var(--gradient-start), var(--gradient-middle), var(--gradient-end)); /* Instagram-like gradient background */
    border-right: 1px solid #f2d64a;
}

.sidebar h2 {
    color: #333;
}

.sidebar ul {
    list-style-type: none;
    padding: 0;
}

.sidebar li {
    margin: 10px 0;
    cursor: pointer;
    color: #1b1d1f;
}
